











import Vue from 'vue';


export default Vue.extend({
  props: {
    template: {
      type: String,
      required: true,
    },
    textStyle: {
      type: Object,
      default: null,
    },
  },
  computed: {
    parts(): string[] {
      return this.template.split(/[\{\}]/g);
    },
  },
  methods: {
    isText(index: number) {
      return index % 2 === 0;
    },
  },
});
